<template>
    <div class="manual-lottery">
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>手动开奖</span>
                    <div class="list-poptip">
                        <p>注：输入开奖号码中，赛车类游戏“10”填写“0”代替，已开奖的手动无效。开奖时间记录执行时间。</p>
                    </div>
                </div>
            </div>
            <div class="query-list">
                <span>彩种</span>
                <i-select v-model="gameType" class="item-select" @on-change="handleChangeGame">
                    <i-option v-for="(item, index) in gameList" :key="index" :value="item.type">{{item.name}}</i-option>
                </i-select>
                <Input v-model="periods" placeholder="输入期号" class="column-input"></Input>
                <Input v-model="lotteryNum" placeholder="输入开奖号码" class="column-input"></Input>
                <div class="open" @click="handleLottery">执行开奖</div>
            </div>
        </div>
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>开奖记录</span>
                </div>
            </div>
            <div class="data-con">
                <div class="data-header">
                    <p class="item-info">游戏类型</p>
                    <p class="item-info">期号</p>
                    <p class="item-info">开奖时间</p>
                    <p class="item-info">开奖结果</p>
                    <p class="item-info">投注金额</p>
                    <p class="item-info">中奖金额</p>
                    <p class="item-info">操作</p>
                </div>
                <div class="data-list">
                    <div class="data-item" v-for="(item, index) in lotteryData" :key="index">
                        <p class="item-info">{{item.name}}</p>
                        <p class="item-info">{{item.id}}</p>
                        <p class="item-info">{{item.time}}</p>
                        <p class="item-info">
                            <span v-for="(item, index) in item.lotteryResult" :key="index" v-html="lotteryStyle.find(ele => ele.value == (item.length === 2 ? item.substr(1,2) : item)).key"></span>
                        </p>
                        <p class="item-info">{{item.betMoney}}</p>
                        <p class="item-info">{{item.winningMoney}}</p>
                        <div class="item-info">
                            <span @click="handleShowInfo(item.id)">详情</span>
                            <span v-if="item.status !== '已开奖'" @click="handleSysLottery(item)">结算</span>
                        </div>
                    </div>
                    <div v-if="!lotteryData.length && showPoptip">
                        <p>{{gameList.find(ele => ele.type === gameType).name + '第' + periods + '期没有获取到开奖号码，请执行开奖'}}</p>
                    </div>
                </div>
            </div>
            <Page class="footer-page" :total="page.total" :page-size="page.pageSize" :current="page.pageNum" show-sizer @on-page-size-change="handleChangeSize" @on-change="handleChangePage"/>
        </div>
        <div v-show="lotteryInfo.show" class="lottery-mask">
            <div v-show="lotteryInfo.show" class="lottery-data">
                <div class="lottery-header">
                    <span>开奖详情</span>
                    <span class="modal-close" @click="handleCloseModal">X</span>
                </div>
                <div class="lottery-list">
                    <i-table :loading="isLoadingDetail" :columns="lotteryInfoCols" :data="lotteryInfoData"></i-table>
                </div>
            </div>
        </div>
        <!-- <loading :loading="showLoading"></loading> -->
    </div>
</template>

<script>
export default {
    data() {
        return {
            showLoading: false,
            isLoadingDetail: false,
            isLoading: false,
            showPoptip: false,
            lotteryInfoData: [],
            periods: '',
            lotteryInfo: {
                show: false
            },
            lotteryInfoCols: [
                {
                    title: '期号',
                    align: 'center',
                    key: 'id'
                },
                {
                    title: '用户Id',
                    align: 'center',
                    key: 'userId'
                },
                {
                    title: '投注时间',
                    align: 'center',
                    key: 'time'
                },
                {
                    title: '投注总额',
                    align: 'center',
                    key: 'betMoney'
                },
                {
                    title: '中奖总额',
                    align: 'center',
                    key: 'winningMoney'
                },
                {
                    title: '投注号码',
                    align: 'center',
                    key: 'betNum'
                },
                {
                    title: '中奖号码',
                    align: 'center',
                    key: 'winningNum'
                }
            ],
            gameType: 1,
            periods: '',
            lotteryNum: '',
            gameList: [],
            lotteryData: [],
            page: {
                total: 0,
                pageSize: 10,
                pageNum: 1
            },
            lotteryStyle: [
                {
                    key: '&#59689;',
                    value: 0
                },
                {
                    key: '&#59690;',
                    value: 1
                },
                {
                    key: '&#59691;',
                    value: 2
                },
                {
                    key: '&#59692;',
                    value: 3
                },
                {
                    key: '&#59693;',
                    value: 4
                },
                {
                    key: '&#59694;',
                    value: 5
                },
                {
                    key: '&#59695;',
                    value: 6
                },
                {
                    key: '&#59696;',
                    value: 7
                },
                {
                    key: '&#59697;',
                    value: 8
                },
                {
                    key: '&#59698;',
                    value: 9
                }
            ]
        }
    },
    beforeRouteEnter (to, from, next) {
        // ...
        next(vm => {
            const type = to.params.type;
            const periods = to.params.periods;
            if (type) {
                vm.gameType = type;
                vm.periods = periods;
            }
            vm.getGameList().then(function() {
                vm.handleQueryLottery();
            })
        })
    },
    methods: {
        getGameList() {
            let that = this;

            return new Promise((resolve, reject) => {
                that.$axios({
                    url: '/api/Merchant/GetGameList'
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.gameList = result.map(item => {
                            return {
                                name: item.GameName,
                                type: item.GameType,
                                key: item.NickName,
                                gameType: item.Type,
                                value: 0
                            }
                        }).sort((a, b) => a.type - b.type);
                        resolve();
                    }
                    reject();
                })
            })
        },
        handleSysLottery(item) {
            let that = this;

            if (!that.isLoading) {
                that.isLoading = true;
                that.showLoading = true;

                that.$axios({
                    url: '/api/Setup/ManualAward',
                    params: {
                        nper: item.id,
                        gameType: that.gameType
                    }
                }).then(function(data) {
                    that.isLoading = false;
                    that.showLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.handleQueryLottery();
                    }
                })
            }
        },
        handleChangeGame() {
            let that = this;

            that.page.pageNum = 1;
            that.handleQueryLottery();
        },
        handleShowInfo(nper) {
            let that = this;

            that.handleQueryLotteryInfo(nper);
            that.lotteryInfo.show = true;
        },
        handleCloseModal() {
            let that = this;
            that.lotteryInfo.show = false;
        },
        handleChangeSize(_pageSize) {
            let that = this;

            that.page.pageSize = _pageSize;
            that.page.pageNum = 1;
            that.handleQueryLottery();
        },
        handleChangePage(page) {
            let that = this;

            that.page.pageNum = page;
            that.periods = '';
            that.handleQueryLottery();
        },
        handleQueryLotteryInfo(_nper) {
            let that = this;

            if (!that.isLoadingDetail) {
                that.isLoadingDetail = true;

                that.$axios({
                    url: '/api/Setup/GetGameLotteryDetailed',
                    params: {
                        gameType: that.gameType,
                        nper: _nper
                    }
                }).then(data => {
                    that.isLoadingDetail = false;
                    if (data.data.Status === 100) {
                        that.lotteryInfoData = [];
                        const result = data.data.Data;

                        that.lotteryInfoData = result.map(item => {
                            return {
                                id: item.IssueNum,
                                userId: item.UserName + '(' + item.OnlyCode + ')',
                                time: item.BetTime,
                                betMoney: item.BetAmount,
                                winningMoney: item.WinAmount,
                                betNum: item.BetNum,
                                winningNum: item.WinNum
                            }
                        })
                    }
                })
            }
        },
        handleQueryLottery() {
            let that = this,
                vParam = {};

            if (!that.isLoading) {
                that.isLoading = true;
                that.showLoading = true;

                vParam['gameType'] = that.gameType;
                vParam['start'] = that.page.pageNum;
                vParam['pageSize'] = that.page.pageSize;
                if (that.periods) {
                    vParam['nper'] = that.periods;
                }

                that.$axios({
                    url: '/api/Setup/SearchGameLottery',
                    params: vParam
                }).then(data => {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.lotteryData = [];
                        const result = data.data.Data;

                        that.page.total = data.data.Total;
                        const _gameType = that.gameList.find(ele => ele.type == that.gameType).gameType;
                        that.lotteryData = result.map(item => {
                            return {
                                name: that.gameList.find(ele => ele.type === that.gameType).name,
                                id: item.IssueNum,
                                time: item.LotteryTime.substr(0, 16),
                                lotteryResult: _gameType == 2 ? item.Nums.split(',').filter(function(item, index) {return index < 5}) : item.Nums.split(','),
                                betMoney: item.BetAmount,
                                winningMoney: item.WinAmount,
                                status: item.Status
                            }
                        })

                        if (that.periods && !that.lotteryData.length) {
                            that.showPoptip = true;
                        }
                    }
                    that.showLoading = false;
                })
            }
        },
        handleLottery() {
            let that = this,
                href = '',
                vData = {},
                lotteryNum = null,
                _gameType = '';

            if (!that.periods.trim()) {
                that.$Message.error("请输入开奖期号");
                return;
            }

            _gameType = that.gameList.find(ele => ele.type == that.gameType).gameType;

            switch(_gameType) {
                case 1:
                    if (that.lotteryNum.length !== 10 || that.lotteryNum.match(/\d/g).length !== 10) {
                        that.$Message.error("请输入正确的开奖号码");
                        return;
                    }
                    lotteryNum = that.lotteryNum.split('').map(item => item.replace('0', '10'));
                    vData = {
                        "IssueNum": that.periods,
                        "One": lotteryNum[0],
                        "Two": lotteryNum[1],
                        "Three": lotteryNum[2],
                        "Four": lotteryNum[3],
                        "Five": lotteryNum[4],
                        "Six": lotteryNum[5],
                        "Seven": lotteryNum[6],
                        "Eight": lotteryNum[7],
                        "Nine": lotteryNum[8],
                        "Ten": lotteryNum[9],
                        "AddTime": new Date().pattern('yyyy-MM-dd HH:mm:ss')
                    };
                    href = '/api/Setup/AddGameLottery10';
                break;
                case 2:
                    if (that.lotteryNum.length !== 5 || that.lotteryNum.match(/\d/g).length !== 5) {
                        that.$Message.error("请输入正确的开奖号码");
                        return;
                    }
                    lotteryNum = that.lotteryNum.split('');
                    vData = {
                        "IssueNum": that.periods,
                        "Wan": lotteryNum[0],
                        "Qian": lotteryNum[1],
                        "Bai": lotteryNum[2],
                        "Shi": lotteryNum[3],
                        "Ge": lotteryNum[4],
                        "AddTime": new Date().pattern('yyyy-MM-dd HH:mm:ss')
                    };
                    href = '/api/Setup/AddGameLottery5';
                break;
            }

            if (!that.isLoading) {
                that.isLoading = true;

                that.$axios({
                    url: href,
                    method: 'post',
                    data: vData,
                    params: {
                        gameType: that.gameType
                    }
                }).then(data => {
                    that.isLoading = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.periods = '';
                        that.handleQueryLottery();
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                })
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.manual-lottery {
    position: relative;
    .content {
        .list-header {
            width: 100%;
            .list-title {
                justify-content: space-between;

                width: 100%;

                .list-poptip {
                    font-weight: 400;
                    color: rgb(102, 102, 102);
                }
            }
        }

        .query-list {
            display: flex;
            align-items: center;

            margin-top: 10px;

            height: 30px;

            & > * {
                flex-shrink: 0;
            }

            & > span {
                padding: 0 7px;
            }

            .item-select {
                margin-right: 25px;

                width: 144px;

                text-align: left;
            }

            .column-input {
                margin-right: 32px;

                width: 202px;
            }

            .open {
                margin-left: 120px;

                width: 122px;
                height: 30px;
                line-height: 26px;

                // background-color: rgb(215, 215, 215);
                // border-radius: 4px;
                // border: solid 1px rgb(188, 188, 188);
                background-color: #00e14a;
                box-shadow: inset 0px 0px 5px 0px rgba(124, 255, 158, 0.75);
                border-radius: 10px;
                border: solid 1px #0e903c;
                text-align: center;
                color: #fff;
                cursor: pointer;
            }
        }

        .data-con {
            margin-top: 10px;

            border: solid 1px #bcbcbc;
            overflow: hidden;

            .data-header, .data-item {
                display: flex;
                align-items: stretch;

                height: 50px;

                .item-info {
                    flex-shrink: 0;

                    width: 80px;
                    line-height: 50px;

                    border-right: solid 1px #d7d7d7;
                    border-bottom: solid 1px #d7d7d7;
                }

                .item-info:nth-child(2) {
                    width: 100px;
                }

                .item-info:nth-child(3) {
                    width: 120px;
                }

                .item-info:nth-child(4) {
                    width: 388px;

                    font-family: jdiconfontA;
                }
            }

            .data-header {
                background-color: rgb(228, 228, 228)
            }

            .data-list {
                .data-item {
                    height: 40px;

                    font-size: 12px;

                    .item-info {
                        line-height: 40px;
                    }

                    .item-info:nth-child(4) {
                        font-size: 28px;
                        color: rgb(13, 119, 199);
                    }

                    .item-info:last-child {
                        display: flex;
                        justify-content: space-around;

                        line-height: 40px;

                        color: rgb(51, 102, 204);
                        cursor: pointer;
                    }
                }

                .data-item:last-child {
                    .item-info {
                        border-bottom: 0;
                    }
                }
            }
        }
    }

    .lottery-mask {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;

        width: 100%;
        height: 100%;

        background-color: rgba(55,55,55,.6);

        z-index: 1000;

        .lottery-data, .manual-lottery {
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            margin: auto;

            width: 1200px;
            height: 450px;

            border: solid 3px #cdcdcd;
            border-radius: 4px;
            background-color: #f2f2f2;
            z-index: 1001;
            text-align: center;

            .lottery-header {
                display: flex;
                justify-content: space-between;

                padding: 5px;

                background-color: #ddd;
                font-weight: bold;

                .modal-close {
                    cursor: pointer;
                }
            }

            .lottery-list {
                margin: 10px;

                border: solid 1px #ddd;
                border-radius: 4px;
            }

            .manual-list {
                padding: 0 20px;

                .manual-item {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;

                    margin: 20px 0;

                    & > span {
                        width: 80px;
                    }
                }
            }
        }

        .manual-lottery {
            width: 400px;
            height: 260px;
        }
    }
}
</style>